@import '../../../../styles/variables.scss';
.items {
  display: grid;
  grid-template-columns: repeat(2, auto);
  row-gap: 56px;
  justify-content: space-between;
  border-radius: 20px;
  border: 1px solid $bg-light;
  padding: 40px;
  margin-bottom: 46px;
  background: linear-gradient($border, $border) no-repeat center/1px 100%
    #f4f4f4;
  @media (max-width: 768px) {
    margin-bottom: 30px;
  }
  @media (max-width: 425px) {
    padding: 20px;
    row-gap: 50px;
    margin-bottom: 20px;
  }
}
.item {
  display: flex;
}
.indicator {
  display: flex;
  align-items: center;
  margin-right: 10px;
  &__name {
    font-weight: 400;
    font-size: 20px;
    line-height: 25px;
  }
  &__value {
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
  }
  @media (max-width: 425px) {
    &__name {
      font-size: 14px;
      line-height: 22px;
    }
    svg {
      width: 20px;
    }
  }
}
